import React from "react";

const ExperienceTable = ({ headers, data, deleteRow }) => {
  return (
    <div>
      <table>
        <tr>
          {headers.map((header) => (
            <th key={header}>{header}</th>
          ))}
        </tr>

        {data.map((row, index) => (
          <tr>
            <td> {row.job_title} </td>
            <td> {row.job_sector} </td>
            <td> {row.is_federal} </td>
            <td> {row.federal_entity_id} </td>
            <td> {row.local_entity_id} </td>
            <td> {row.company} </td>
            <td> {row.start_date} </td>
            <td> {row.end_date} </td>
            <td>
              {" "}
              {row.references.map((refrnc) => (
                <tr>
                  {" "}
                  <td> {refrnc.name} </td> <td> {refrnc.phone} </td>{" "}
                </tr>
              ))}{" "}
            </td>
            <td onClick={() => deleteRow(index)}> Delete </td>
          </tr>
        ))}
      </table>
    </div>
  );
};

export default ExperienceTable;
