import React from 'react'


const EducationHistoryTable = ({headers, data, deleteRow})=>{


    return(
        <div>
            <table>
  <tr>
      {headers.map((header) => <th key={header}>{header}</th>  )}
  </tr>

        {data.map((row, index) =>  <tr>
    <td> {row.feild_of_study} </td>
    <td> {row.institute_id} </td>
    <td> {row.institute_name} </td>
    <td> {row.year_of_graduation} </td>
    <td> {row.degree} </td>
    <td onClick={()=> deleteRow(index)}> Delete </td>
   
  </tr>)}
  
  
</table>

        </div>
    )
}

export default EducationHistoryTable