import React from 'react'


const RefferenceTable = ({headers, data, deleteRow})=>{


    return(
        <div>
            <table>
  <tr>
      {headers.map((header) => <th key={header}>{header}</th>  )}
  </tr>

        {data.map((row, index) =>  <tr>
    <td> {row.name} </td>
    <td> {row.phone} </td>
    <td onClick={()=> deleteRow(index)}> Delete </td>
   
  </tr>)}
  
  
</table>

        </div>
    )
}

export default RefferenceTable